.b {
    background-color: black;
}

.nav {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    /*background-color: #73628A;*/
    padding: 10px;
    border-radius: 4px;
    margin-top: 10px;
}

.main-header {
    margin-top: 150px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-right: 50px;
    margin-left: 50px;
}

.landing {
    margin-right: 20px;
    margin-left: 20px;
    margin-top: 50px;
    height: 200px;
    background-color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    border-radius: 10px;
}

.nav a {
    margin-right: 6px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-right: 15px;
    padding-left: 15px;
    font-size: 18px;
    background-color: #ff8552;
    font-family: "Poppins", sans-serif;
    color: black;
    font-weight: bolder;
    text-decoration: none;
    border-radius: 5px;
    border-color: #001524;
}

.content {
    font-size: 25px;
}

.footer {
    background-color: #73628a;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    text-align: center;
    padding-top: 15px;
    padding-bottom: 15px;
    font-family: "Poppins", sans-serif;
}

.footer a {
    color: #3c3744;
    font-weight: bold;
    font-size: 20px;
}

.projects-row{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    justify-self: center;
    -webkit-align-items: center;
            align-items: center;
}

.projects {
    margin-top: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.projects input {
    width:  200px;
}

.main-landing {
    height: 100%;
    widows: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}

/*CARD*/

.contact {
    font-family: "Poppins";
    border-radius: 5px;
    margin-top: 20px;
    height: auto;
    width: 350px;
    background-color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding-bottom: 10px;
}

.contact ul li {
    list-style: none;
    margin-left: -10px;
}

.card {
    padding-bottom: 10px;
    width: 50%;
    background-color: #F8BD7F;
    margin-bottom: 10px;
    border-radius: 5px;
}

.card h1 {
    font-family: "Poppins";
    text-align: center;
}

.card-flex {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding-left: 20px;
    padding-right: 20px;
}

.card-one {
    background-color: #38726C;
    border-radius: 5px;
    height: 120px;
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 10px;
}

.card-one h2 {
    text-decoration: underline;
}

.card-one p {
    margin-top: -5px;
}

/*Width & Height matter in Flex Box*/
.card-content {
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.card-tag{
    padding: 5px;
    width: 60%;
    border-radius: 20px;
    text-align: center;
    background-color: #F28123;
    margin-bottom: 5px;
}

.lang{
    font-weight: bold;
    font-size: 20px;
    list-style: none;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 7px;
    display: inline;
    background-color: #FFA5AB;
}


.search {
    border-radius: 50px;
}

/*Search*/


@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: portrait) {
    .card {
        width: 350px;
        height: 250px;
    }
    .card-one {
        height: 170px;
    }

    .card-tag{
        width:  120px;
    }
}

.blog {
	background:  white;
	padding-right: 50px;
	padding-left: 50px;
	text-align: justify;
	margin-left: 50px;
	margin-right: 50px;
	border-radius: 10px;
	padding-top: 5px;
}

.blog p{
	font-size: 20px;
}

.blog h1{
	font-size: 60px;
}

.blog div{
	font-family: 'Poppins';
}

@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: portrait) {
 	.blog p{
 		font-size: 14px;
 	} 
 	.blog h1{
 		font-size: 50px;
 	}
}

